import { Injectable } from  '@angular/core';
import { HttpClient } from  '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

export  interface  Item{
  name:  string;
  description:  string;
  url:  string;
  html:  string;
  markdown:  string;
}

@Injectable({
providedIn:  'root'
})

export  class  DataService {

  private  dataURL:  string  =  "https://www.techiediaries.com/api/data.json";
  private subsHost = "http://localhost:3000";
  private subsAPI = "/subscribe";

  constructor(private  httpClient:  HttpClient) {}

  fetch():  Observable<Item[]>{
    return <Observable<Item[]>>this.httpClient.get(this.dataURL);
  }

  postSubscribe(sub) {
    let URL = this.subsHost + this.subsAPI;
    return this.httpClient.post(URL, sub).pipe(catchError(err => {
        console.log('Handling error for subscription', err);
        return throwError(err);
      }
    ));
  
  }
}
